import layout from '@/page/layout'

export default [
  {
    path: '/login',
    name: 'login',
    component: () => import('@/page/login')
  },
  {
    path: '/',
    name: 'layout',
    component: layout,
    children: [
      {
        path: '/', 
        name: 'home', 
        component: () => import('@/page/home/home')
      },
      {
        path: '/gadd', 
        name: 'gadd', 
        component: () => import('@/page/goods/gadd')
      },
      {
        path: '/glist', 
        name: 'glist', 
        component: () => import('@/page/goods/glist')
      },
      {
        path: '/mlist', 
        name: 'mlist', 
        component: () => import('@/page/merchants/mlist')
      },
      {
        path: '/olist', 
        name: 'olist', 
        component: () => import('@/page/order/olist')
      },
      {
        path: '/ulist', 
        name: 'ulist', 
        component: () => import('@/page/user/ulist')
      }
    ]
  }
]



