<style scoped>
.layout{
    position: relative;
    overflow: hidden;
}
.layout-logo{
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
    width: 115px;
    height: 29px;
    border-radius: 3px;
    background: url("../assets/img/logo.png") no-repeat;
    background-size: 100% 100%;
}
.layout-nav{
    float: right;
}
.layout-footer-center{
    text-align: center;
}
</style>
<template>
    <div class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <div class="user-avator-dropdown">
                                <Dropdown>
                                  <!-- <Avatar :src="userAvator"/> -->
                                  <Avatar>1321</Avatar>    
                                  <Icon :size="18" type="md-arrow-dropdown"></Icon>
                                  <DropdownMenu slot="list">
                                    <DropdownItem name="logout">退出登录</DropdownItem>
                                </DropdownMenu>
                            </Dropdown>
                        </div>
                    </MenuItem>
                </div>
            </Menu>
        </Header>
        <Layout>
            <Sider hide-trigger :style="{background: '#fff'}">
                <Menu theme="light" width="auto" @on-select="openView">
                    <MenuGroup title="后台管理列表">
                        <Submenu name="商品管理">
                            <template slot="title">
                                <Icon type="ios-archive" />
                                商品管理
                            </template>
                            <MenuItem name="/gadd">新增商品</MenuItem>
                            <MenuItem name="/glist">商品列表</MenuItem>
                        </Submenu>
                        <Submenu name="2">
                            <template slot="title">
                             <Icon type="md-reorder" />
                             订单管理
                         </template>
                         <MenuItem name="/olist">订单列表</MenuItem>
                     </Submenu>
                     <Submenu name="3">
                        <template slot="title">
                            <Icon type="md-ribbon" />
                            商户管理
                        </template>
                        <MenuItem name="/mlist">商户列表</MenuItem>
                    </Submenu>
                    <Submenu name="4">
                        <template slot="title">
                            <Icon type="ios-people" />
                            用户管理
                        </template>
                        <MenuItem name="/ulist">用户列表</MenuItem>
                    </Submenu>
                </MenuGroup>
            </Menu>
        </Sider>
        <Layout :style="{padding: '0 24px 12px 24px'}">
            <Breadcrumb :style="{margin:'0 -24px 10px -24px',padding:'12px 0 12px 12px',backgroundColor:'#fff'}">
                <BreadcrumbItem to="/">
                    <Icon type="ios-home-outline"></Icon>首页
                </BreadcrumbItem>
                <BreadcrumbItem :to="item.path" v-for="item in breadcrumbs">
                    <Icon :type="item.icon"></Icon>{{item.name}}
                </BreadcrumbItem>
            </Breadcrumb>
            <Content>
                <router-view/>
            </Content>
        </Layout>
    </Layout>
</Layout>
</div>
</template>
<script>

export default {
    data () {
        return {
            breadcrumbs:[]
            // nowRouter:this.$route.path
        }
    },
    created () {
        console.log('当前store是：'+this.$store);
    },
    methods: {
        openView (name) {
            var that = this;
            that.$router.push(name);
            that.breadcrumbs = [];
            var tmp = {};
            tmp.path = name;
            tmp.icon = "";
            tmp.name = "";
            switch (name){
                case "/gadd":
                tmp.name = "新增商品";
                break;
                case "/glist":
                tmp.name = "商品列表";
                break;
                case "/mlist":
                tmp.name = "商户列表";
                break;
                case "/olist":
                tmp.name = "订单列表";
                break;
                case "/ulist":
                tmp.name = "用户列表";
                break;
            }
            this.breadcrumbs.push(tmp);
        },
        backHome (){
            alert(1);
            this.breadcrumbs = [];
        }
    }        
}
</script>



